"use client";

import { useState } from "react";
import { useSession } from "next-auth/react";
import { Form, Button, Alert } from "react-bootstrap";

export default function AccountSection() {
  const { data: session } = useSession();
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [status, setStatus] = useState("idle");      // <- sin gen√©ricos
  const [error, setError] = useState(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setStatus("loading");
    const res = await fetch("/api/auth/change-password", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ currentPassword, newPassword }),
    });
    const payload = await res.json();
    if (!res.ok) {
      setError(payload.error);
      setStatus("error");
    } else {
      setError(null);
      setStatus("ok");
      setCurrentPassword("");
      setNewPassword("");
    }
  };

  return (
    <div className="text-center">
      <h3>Mi cuenta</h3>
      <p><strong>Nombre:</strong> {session.user.name}</p>
      <p><strong>Email:</strong> {session.user.email}</p>

      <hr />

      <h5>Cambiar contrase√±a</h5>
      <Form onSubmit={handleSubmit} style={{ maxWidth: 400, margin: "0 auto" }}>
        {status === "ok" && <Alert variant="success">Contrase√±a actualizada</Alert>}
        {status === "error" && <Alert variant="danger">{error}</Alert>}

        <Form.Group className="mb-3">
          <Form.Label>Contrase√±a actual</Form.Label>
          <Form.Control
            type="password"
            required
            value={currentPassword}
            onChange={(e) => setCurrentPassword(e.target.value)}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Nueva contrase√±a</Form.Label>
          <Form.Control
            type="password"
            required
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
          />
        </Form.Group>

        <Button
          type="submit"
          className="btn-orange w-100"
          disabled={status === "loading"}
        >
          {status === "loading" ? "Guardando‚Ä¶" : "Guardar"}
        </Button>
      </Form>
    </div>
  );
}
